import { getRandInteger, makeUid } from "../../utils/random";
import { LABORER, WORKING_AGE } from "../Industry/utils";

export const MALE = "M";
export const FEMALE = "F";

export const seedRandomPopulation = cityName => {
  debugger;
  let people = {};

  /* generate random starting population number */
  let totPop = getRandInteger(5, 10);
  if (totPop % 2 != 0) {
    totPop = totPop + 1;
  }

  /* generate equal number of persons of each gender */
  let gender = MALE;
  for (let i = 0; i < totPop; i++) {
    let person = makeNewPerson(cityName, undefined, gender);
    while (people[person.name]) {
      person = makeNewPerson(cityName, undefined, gender);
    }

    people[person.name] = person;

    if (gender == MALE) {
      gender = FEMALE;
    } else {
      gender = MALE;
    }
  }

  return {
    ppl: people
  };
};

export const makeNewPerson = (cityName, age, gender) => {
  let occupation = "N/A";
  if (!gender) {
    gender = getRandInteger(0, 1) == 1 ? MALE : FEMALE;
  }
  if (!age) {
    age = getRandInteger(20, 30);
  }
  if (age > WORKING_AGE) {
    occupation = LABORER;
  }

  return {
    name: cityName + "_pop_ppl_" + makeUid(),
    age: age,
    gender: gender,
    occupation: occupation
  };
};

export const createFamily = (MALE, FEMALE) => {};



