import { compose, createStore, applyMiddleware, combineReducers } from "redux";
import { add_city_reducer } from "./components/AddCity/reducers";
import { city_reducer } from "./modules/City/reducers";
import { time_reducer } from "./modules/Time/reducers";
import reduceReducers from "reduce-reducers";
import logger from "redux-logger";
import storeSynchronize from "redux-localstore";
/**import thunk from 'redux-thunk';**/

const appReducers = reduceReducers(
  add_city_reducer,
  city_reducer,
  time_reducer
);

const composeEnhancers =
  typeof window === "object" && window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({
        // Specify extension’s options like name, actionsBlacklist, actionsCreators, serialize...
      })
    : compose;

const enhancer = composeEnhancers(
  applyMiddleware(logger /**, thunk**/)
  // other store enhancers if any
);

export const store = createStore(appReducers, enhancer);

storeSynchronize(store);



