import { ADD_CITY } from "./actions";
import { defineState } from "redux-localstore";
import { getState } from "redux-localstore";

const defaultState = getState() || {
  cities: null
};

const initialState = defineState(defaultState)("add_city_reducer");

export function add_city_reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_CITY:
      let newCities = {};
      if (state.cities) {
        newCities = Object.assign({}, state.cities);
      }
      newCities[action.payload.city.name] = action.payload.city;
      let newPpl = action.payload.ppl;
      if (state.ppl) {
        newPpl = Object.assign({}, state.ppl, action.payload.ppl);
      }
      return {
        ...state,
        cities: newCities,
        ppl: newPpl
      };
    default:
      return state;
  }
}



