import React, { Component } from "react";

import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import { Time } from "../../modules/Time";
import { getCityName } from "../../utils/path";

const styles = {
  card: {
    maxWidth: "100%"
  }
};

class CityDash extends React.Component {
  state = {
    name: ""
  };

  render() {
    const { classes, cities } = this.props;
    return (
      <div>
        City Dash {getCityName(this.props.location.pathname)}
        <Time />
      </div>
    );
  }
}

export default withStyles(styles)(CityDash);



