import React, { Component } from "react";

import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import CheckCircle from "@material-ui/icons/CheckCircle";
import CheckCross from "@material-ui/icons/Cancel";
import { Link } from "react-router-dom";
import { withRouter } from "react-router";

const styles = {
  card: {
    maxWidth: "100%"
  }
};

class AddCity extends React.Component {
  state = {
    name: ""
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
  };

  handleSubmit = () => {
    this.props.dispatchAddCity(this.state.name);
    this.props.history.goBack();
  };

  render() {
    const { classes, dispatchAddCity, cities } = this.props;
    return (
      <form className={classes.container} noValidate autoComplete="off">
        <TextField
          id="add-city"
          label="City Name"
          placeholder=""
          className={classes.textField}
          margin="normal"
          value={this.state.name}
          onChange={this.handleChange("name")}
        />
        <br />

        <Button size="large" color="primary" onClick={this.handleSubmit}>
          <CheckCircle />
        </Button>
        <Link to="/cns">
          <Button size="large" color="primary">
            <CheckCross />
          </Button>
        </Link>
      </form>
    );
  }
}

AddCity.propTypes = {
  classes: PropTypes.object.isRequired
};

export default withRouter(withStyles(styles)(AddCity));



