import React from "react";
import { connect } from "react-redux";
import AddCity from "./AddCity";
import { addCity } from "./actions";
import { withRouter } from "react-router-dom";
import { seedRandomPopulation } from "../../modules/Population/utils";

const mapDispatchToProps = dispatch => ({
  dispatchAddCity: cityName => {
    let startingPop = seedRandomPopulation(cityName);
    let payload = {
      city: {
        name: cityName,
        ppl: Object.keys(startingPop.ppl),
        singles: Object.keys(startingPop.ppl)
      },
      ppl: startingPop.ppl
    };
    console.log("====city====");
    console.log(payload);
    dispatch(addCity(payload));
  }
});

const mapStateToProps = state => {
  return {
    cities: state.cities
  };
};

const AddCityContainer = connect(mapStateToProps, mapDispatchToProps)(AddCity);

export default AddCityContainer;



