import React from "react";
import { connect } from "react-redux";
import CityDash from "./CityDash";

const mapDispatchToProps = dispatch => ({});

const mapStateToProps = state => {
  return {
    cities: state.cities
  };
};

const CityDashContainer = connect(mapStateToProps, mapDispatchToProps)(
  CityDash
);

export default CityDashContainer;



