import React from "react";
import { connect } from "react-redux";
import Time from "./Time";
import { tickTime } from "./actions";
import { getCityName } from "utils/path";
import { withRouter } from "react-router";

const mapDispatchToProps = dispatch => ({
  dispatchTickTime: cityName => {
    dispatch(tickTime(cityName));
  }
});

const mapStateToProps = (state, ownProps) => {
  return {
    time: state.cities[getCityName(ownProps.location.pathname)].time
  };
};

const TimeContainer = connect(mapStateToProps, mapDispatchToProps)(Time);

export default withRouter(TimeContainer);



